.form-control input{
  color: grey;
  font-size: 1.2rem;
  width: 100%;

}

.card-grid {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.gift-card.card {
  align-content: center;
  border: 1px solid grey;
  border-radius: 6px;
  margin-bottom: 10px;
  margin-right: 10px;
  overflow: hidden;
}

.gift-card.card p {
  padding: 5px;
  text-align: center;
}

.gift-card.card img {
  max-height: 170px ;
}